@charset "UTF-8";
#web-pay {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0 20px;
  line-height: 1.6; }
  #web-pay .info {
    margin: 30px 0;
    text-align: center; }
    #web-pay .info .amount {
      margin-left: 10px;
      font-size: 30px;
      color: #333; }
      #web-pay .info .amount span {
        margin-left: 3px;
        font-size: 13px; }
    #web-pay .info .desc {
      padding: 0 20px;
      font-size: 13px;
      color: #999; }
  #web-pay .payment {
    border-top: 1px solid;
    margin-left: -20px;
    margin-right: -20px;
    border-color: #f0f0f0; }
    #web-pay .payment .checkbox {
      position: relative;
      padding: 8px 20px;
      border-bottom: 1px solid;
      height: 60px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      border-color: #f0f0f0; }
      #web-pay .payment .checkbox .pay-icon {
        font-size: 28px;
        vertical-align: middle; }
      #web-pay .payment .checkbox span {
        margin-left: 5px;
        font-size: 17px;
        vertical-align: middle;
        color: #333; }
      #web-pay .payment .checkbox .design-radio {
        float: right;
        margin-top: 8px; }
    #web-pay .payment .alipay .pay-icon {
      color: #039ae3; }
    #web-pay .payment .wechat .pay-icon {
      color: #22ab39; }
  #web-pay .submit {
    display: block;
    margin-top: 40px;
    height: 48px;
    border-radius: 4px;
    background-color: #ea6f5a;
    text-align: center;
    line-height: 48px;
    font-size: 17px;
    color: #fff;
    width: 100%; }

button {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
  border: none;
  background-color: transparent;
  cursor: default;
  -webkit-tap-highlight-color: transparent;
  text-transform: none;
  overflow: visible; }

.design-radio {
  position: relative;
  line-height: 30px;
  display: inline-block;
  /*设置选中的input的样式*/
  /* + 是兄弟选择器,获取选中后的label元素*/
  /*添加的加号与label进行拼接(一个矩形边框去掉上和左的边框),再旋转45度*/ }
  .design-radio input[type='radio'] {
    width: 20px;
    height: 20px;
    opacity: 0; }
  .design-radio label {
    position: absolute;
    left: 5px;
    top: 3px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #999;
    　　　　　　　cursor: pointer; }
  .design-radio input:checked + label {
    background-color: #fe6d32;
    border: 1px solid #fe6d32; }
  .design-radio input:checked + label::after {
    position: absolute;
    content: '';
    width: 5px;
    height: 10px;
    top: 3px;
    left: 6px;
    border: 2px solid #fff;
    border-top: none;
    border-left: none;
    transform: rotate(45deg); }
